<template>
  <el-button type="primary" icon="el-icon-circle-check" @click="click"></el-button>
</template>

<script>
export default {
  name: 'confirm-select',
  methods: {
    click() {
      this.$emit('handleConfirmSelect');
    },
  },
};
</script>



