<template>
  <div>
      <el-select v-model="options1.fieldCode" placeholder="" size="small" style="width:160px">
        <el-option
          v-for="item in fields"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <el-select v-model="options1.fileType" placeholder="" size="small" style="width:100px">
        <el-option
          v-for="item in conditions"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <el-input placeholder="" v-model="options1.defValue" size="small" style="width:160px">
      </el-input>
      <el-select v-model="options1.logic" placeholder="" size="small" style="width:80px">
        <el-option
          v-for="item in ands"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
  </div>
</template>
<script>
export default {
  name: 'searchgSub',
  data() {
    return {
      ands: [{
        value: 'AND',
        label: '',
      }, {
        value: 'OR',
        label: '',
      }],
      conditions: [{
        value: 'neq',
        label: '',
      }, {
        value: 'eq',
        label: '',
      }, {
        value: 'gt',
        label: '',
      }, {
        value: 'lt',
        label: '',
      }, {
        value: 'gte',
        label: '',
      }, {
        value: 'lte',
        label: '',
      }, {
        value: 'leftContain',
        label: '',
      }, {
        value: 'rightContain',
        label: '',
      }, {
        value: 'in',
        label: 'in',
      }, {
        value: 'contain',
        label: '',
      }, {
        value: 'isNotNull',
        label: '',
      }, {
        value: 'isNull',
        label: '',
      }],
      options1: this.options,
    };
  },
  props: {
    fields: {
      type: Array,
      default() {
        return [];
      },
    },
    options: {
      type: Object,
      default() {
        return {
          fieldCode: '',
          fileType: '',
          defValue: '',
          logic: '',
        };
      },
    },
  },
};
</script>



